import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PopoverFormsComponent } from './popover-forms/popover-forms.component';
import { CreateNewComponent } from './inventory/create-new/create-new.component';
import { InventoryEditComponent } from './inventory/inventory-edit/inventory-edit.component';
import { LayoutComponent } from './layout/layout.component';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './auth.guard';
import { NotFoundComponent } from './inventory/not-found/not-found.component';


const routes: Routes = [
  {path: 'login', component: LoginComponent},
  {path: '', component: LayoutComponent, pathMatch: 'full', canActivate: [AuthGuard]},
  {path: 'add-room', component: PopoverFormsComponent, canActivate: [AuthGuard]},
  {path: 'inventory', component: LayoutComponent, canActivate: [AuthGuard]},
  {path: 'add-new-inventory', component: CreateNewComponent, canActivate: [AuthGuard]},
  {path: 'edit-inventory', component: InventoryEditComponent, canActivate: [AuthGuard]},


  {path: '**', component: NotFoundComponent, canActivate: [AuthGuard]},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
